@import "../../styles/variables.scss";
.tableSection {
  width: 100%;
  max-width: 100vw;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 72px;
}

.wrapperDays {
  max-width: 1287px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto 52px auto;
}

.day {
  width: 183px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-right: 3px solid $font-black;
  &:last-child {
    border-right: none;
  }
}

.dayTitle {
  font-family: $poppins;
  color: $font-black;
  font-weight: 400;
  font-size: 36px;
}

.verticalLine {
  transform: rotate(90deg);
  width: 63px;
  height: 2px;
  background-color: $font-black;
}

.wrapperTableButtons {
  margin: 0 auto;
  width: 835px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.calendarController {
  width: 200px;
  display: flex;
  justify-content: space-between;
  margin: 0 auto 49px auto;
}

.calendarControllerText {
  font-family: $poppins;
  color: $font-black;
  font-weight: 400;
  font-size: 24px;
  cursor: pointer;
}

.calendarControllerButton {
  background-color: transparent;
  font-size: 28px;
  font-weight: 400;
  font-family: $poppins;
  position: relative;
  top: -1px;
  width: 17px;
  height: 17px;
}

.dataPicker {
  width: 330px !important;
  position: absolute;
  top: 410px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.tableButton {
    font-size: 32px !important;
    padding: 15px 31px !important;
    border-radius: 59px !important;
    transition: 200ms linear;
    &:hover {
        @extend %hover-shadow;
    }
}

.templateText {
  font-size: 24px;
  font-weight: 400;
  font-family: $poppins;
  color: $font-black;
  margin: 0 70px 37px auto;
}

.tableButtonDisabled {
  background-color: $tableItem-grayHover !important;
  cursor: none;
  pointer-events: none;
}